import HeaderCop from '../common/header.vue';
import BScroll from '../common/scrollView.vue';
import { API, getQuery } from '../../../services';
export default {
   
    components: {
        HeaderCop,
        BScroll,
    },
    computed: {
        
    },
    data() {
        return {
            typeList:[],
           title:"绘本订阅计划使用规则",
           titleShow:false,
        }
    },
    mounted () {
         if(this.$route.query.type=='H5'){
            this.titleShow=true;
        }else{
            this.titleShow=false;
        }
        setTimeout(()=>{
            this.typeList=[1,1];
            this.$refs.scollView.scroll.refresh();
        },100);
    },
    activated () {
        if(this.$route.query.type=='H5'){
            this.titleShow=true;
        }else{
            this.titleShow=false;
        }
        setTimeout(()=>{
            this.typeList=[1,1];
            this.$refs.scollView.scroll.refresh();
        },100);
    }

}



