
import HeaderCop from '../common/header.vue';
import BookList from '../common/bookList.vue';
import BScroll from '../common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
directives: {
TransferDom
},
components: {
BookList,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
BScroll,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
confrim: "",
toast: false,
haveData: false,

bookList: [],
title: "智能选书",
iconList: [],
idList: [],
chooseId: 0,
dataRefrese: false,//数据是否刷新完成
param: {
}
}
},
methods: {
goInfo(id){
window.location.href="/#/book_bookInfo/"+id;
},
routerBack() {
this.$router.goBack();
},
/* 更换 type 1单选 2多选  */
change(type, item={}, index = -1) {
let book_ids = [];
for (const item of this.bookList) {
book_ids.push(item.book_id);
}
this.param.type = type;
this.param.book_ids = book_ids;
API.book.intelligentChange(
this.param
).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
this.bookList[index] = res.body.data[0];
} else {
this.bookList = [];
this.bookList = res.body.data;
}
this.$nextTick(() => {
this.scollRefresh();
this.idList = [];
for (const item of this.bookList) {
this.idList.push(item.book_id);
}
})
}
})

},
/**@argument
* 加入购物车
*/
addCar(item) {
if (!this.getUserInfoUserId) {
this.$router.push({
path: "/login"
});
return false;
}
API.book.addCar({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_ids: this.idList
}).then((res) => {
if (res.body.code == 200) {
this.confrim="加入成功";
this.toast=true;
}else if(res.body.code == 250){
this.$router.push({
path:"/book_bookCard"
})
}
})

},
/**@argument
* 列表刷新
*/
listRefresh() {
this.bookList = [];
this.getList();
},
/**@argument
* 获取列表数据
*/
getList() {
API.book.search(this.param).then((res) => {
if (res.body.code == 200) {
this.bookList = res.body.data;
this.$nextTick(() => {
this.scollRefresh();
this.idList = [];
for (const item of this.bookList) {
this.idList.push(item.book_id);
}
})
}
})
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},

},
mounted() {

},
activated() {
this.param = JSON.parse(this.$route.query.param);
this.iconList = JSON.parse(this.$route.query.icon);
/* 类型数据拼接 */
this.listRefresh();
}
}



