
import { mapGetters } from 'vuex';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { API, getQuery } from '../../services';

export default{
data(){
return {
name:'',
certNo:'',
toast:false,
confrim:""
}
},
components: {
XInput,
XButton,
Group,
Cell,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods:{
routerBack(){
this.$router.goBack();
},
userIdentityInfo(){
this.check();
API.person.handleUserIdentityInfo({
user_id: this.getUserInfoUserId,
name:this.name,
certNo:this.certNo,
option:1
}).then((res) => {
if (res.body.code == 200) {
window.location.href = res.body.data.auth_url;
localStorage.setItem("isCertify","4");
this.$store.dispatch('IsCertify');
}
})
},
check(){
if(!this.name){
this.confrim="请输入姓名";
this.toast=true;
return false;
}
}
},
mounted(){

}
}



