
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Scroller,
ListCompent,
LoadMore
},
data () {
return {
canBottom:true,
loadshow:false,
goodsList:[],
/* 搜索栏目显示的名称 */
goodsVal:"",
categoryId:"",
haveData:true,//是否有未加载的数据
searchType:0,
page:1,
pullupConfig: {
content: '上拉加载更多',
downContent: '松开进行加载',
upContent: '上拉加载更多',
loadingContent: '加载中...'
},
typeList:[
{
name:"默认排序",
select:true,
},
{
name:"销量优先",
select:false,
}
]
}
},

methods:{
routerBack(){
this.$router.goBack();
},
/* 选项的切换，切换默认排序和销量优先 */
typeCheck(index){
/* 当前按钮已经处于被选中该状态 */
if(this.typeList[index].select){
return false;
}else{
this.typeList[index].select=true;
this.typeList[1-index].select=false;
/* 分页重置 */
this.page=1;
this.canBottom=true;
}
/* 当前页面拥有categoryId,说明属于分类详情页面 */
if(this.categoryId){
API.main.searchGoods({
goods_category_id:this.categoryId,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}else{
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:index,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}
},
/* 数据初始化 */
Initialization(){
/* 当前属于从分类中进入的接口*/
if(this.$route.query.categoryId){
/* 搜索栏赋值 */
this.goodsVal='';
this.categoryId=this.$route.query.categoryId;
API.main.searchGoods({
goods_category_id:this.$route.query.categoryId,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}else{
/* 来自搜索页面 */
this.goodsVal=this.$route.query.goods_name;
this.categoryId="";
API.main.searchGoods({
goods_name:this.goodsVal,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}).then((Response)=>{
this.goodsList=Response.body.data.shopList.data;
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}
},
/* 加载更多 */
getMoreData(){
if(this.haveData){
this.loadshow=true;
let ajaxData={};
if(this.categoryId){
ajaxData={
goods_category_id:this.$route.query.categoryId,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}
}else{
ajaxData={
goods_name:this.goodsVal,
goods_sort:this.searchType,
page_number:10,
page:this.page,
}
}
API.main.searchGoods(ajaxData).then((Response)=>{
setTimeout(()=>{
this.goodsList=this.goodsList.concat(Response.body.data.shopList.data);
this.canBottom=true;
this.loadshow=false;
},500);
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}else{
this.canBottom=true;
}
}
}
,mounted(){
overscroll(document.querySelector('.goodsList_List'));
this.page=1;
this.canBottom=true;
this.haveData=true;
this.Initialization();
let self = this;
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (document.querySelector(".goodsList_List")) {
bodyScrollTop = document.querySelector(".goodsList_List").scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}

function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (document.querySelector(".goodsList_List")) {
bodyScrollHeight = document.querySelector(".goodsList_List").scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}

function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
document.querySelector(".goodsList_List").onscroll = function() {
if (getScrollTop() + getWindowHeight() >= (getScrollHeight()-10)) {
if(self.canBottom==true){
self.canBottom=false;
self.getMoreData();
}
}
};
}
,activated(){
overscroll(document.querySelector('.goodsList_List'));
if(window.localStorage.getItem("listReload")){
this.page=1;
this.canBottom=true;
this.haveData=true;
this.Initialization();
window.localStorage.setItem("listReload",'');
}else{
setTimeout(() => {
document.querySelector(".goodsList_List").scrollTop = localStorage.getItem("scrolltop");
}, 50);
}

}
}



