import { mapGetters } from 'vuex';
import { API, getQuery } from '../../services';

export default{
    data(){
        return {
            isShow:false,
            zmscore:0,
            relief_limit:0
        }
    },
    computed: {
        ...mapGetters([
            'getUserInfoUserId',
            'getUserInfoToken',
        ])
    },
    methods:{
        routerBack(){
            this.$router.goBack();
        },
        getAuth(){
            if(localStorage.getItem('userInfo')){
                this.$router.push({
                    path:'/authPage'
                })
            }else{
                this.$router.push({
                    path:'/login'
                })
            } 
        },
        userZMScore() {
            API.person.getUserZMScore({
                user_id: this.getUserInfoUserId
            }).then((res) => {
                // console.log(res);
                if (res.body.code == 200) {
                    if(res.body.msg == "获取成功"){
                        this.zmscore = res.body.data.zmscore;
                        this.isShow = true;
                    }
                }
            })
        },
        userZMReliefInfo(){
            API.person.getUserZMReliefInfo({
                user_id: this.getUserInfoUserId
            }).then((res) => {
                // console.log(res)
                if (res.body.code == 200) {
                    this.relief_limit = res.body.data.relief_limit
                }
            })
        }
    },
    activated () {
       this.userZMScore();
       this.userZMReliefInfo(); 
    },
    mounted(){
        
    }
}




