
import HeaderCop from '../common/header.vue';
import BookList from '../common/bookList.vue';
import BScroll from '../common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
directives: {
TransferDom
},
components: {
BookList,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
BScroll
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
haveData: false,
/* 上拉加载更多 */
swiper_pullUp: false,//显示加载
swiper_nodata: false,//没有更多数据
bookList: [],
title: "选书",
iconList: [],
page: 1,
chooseId: 0,
dataRefrese: false,//数据是否刷新完成
param: {
},
loading: false,
}
},
methods: {
chooseType(index) {
if (index == this.chooseId) {
return false;
}
if (this.dataRefrese) {
this.chooseId = index;
}
this.param.order_method = index;
this.$vux.loading.show({
text: 'Loading'
});


this.listRefresh();
},
/**@argument
* 列表刷新
*/
listRefresh() {
this.param.page = 1;
this.bookList = [];
this.getList();
},
/**@argument
* 获取列表数据
*/
getList() {
if (this.loading) {
return false;
}
this.loading = true;
if (this.bookList.length >= 10) {
this.swiper_pullUp = true;
this.swiper_nodata = false;
}
this.dataRefrese = false;
API.book.search(this.param).then((res) => {
if (res.body.code == 200) {
let time = 0;
if (this.bookList.length != 0) {
time = 500;
}
setTimeout(() => {
if (res.body.data.length > 0) {
this.swiper_pullUp = false;
this.bookList = this.bookList.concat(res.body.data);
this.param.page++;
} else {
this.swiper_pullUp = false;
if (this.bookList.length >= 6) {
this.swiper_nodata = true;
}
}
this.$nextTick(() => {
this.scollRefresh();
this.dataRefrese = true;
this.loading = false;
this.$vux.loading.hide();
})
if (this.bookList.length == 0) {
this.haveData = true;
} else {
this.haveData = false;
}
}, time);
}
})
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},

},
mounted() {

},
activated() {
this.param = JSON.parse(this.$route.query.param);
this.listRefresh();
}
}



