
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import value2name from 'vux/src/filters/value2name.js'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
Group,
Cell,
XButton,
XTextarea,
XInput,
XAddress,
CheckIcon,
Toast
},
data () {
return {
confrim:"",
toast:false,
success:false,
title:"新增地址",
addressData:ChinaAddressV3Data,
name:"",
phone:"",
address:[],
/* 详细地址 */
addressDetils:"",
isDefault:false,
/* 用户id */
id:"",
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods:{
routerBack(){
this.$router.goBack();
},
getName (value) {
return value2name(value, ChinaAddressV3Data)
},
/* 检查当前状态属于新增还是编辑状态，通过路由parmas参数 */
typeCheck(){
/* 当前属于新增状态 */
if(this.$route.params.id=="add"){
this.title="新增地址";
this.name="";
this.phone="";
this.address=['北京市','市辖区','东城区'];
this.addressDetils="";
this.isDefault=false;
}else{
this.title="地址编辑";
let id=this.$route.params.id;
this.id=id;
this.getAddress(id);
}
},
/* 编辑时获取地址详情 */
getAddress(id){
API.person.addressInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
id:id,
is_default:0
}).then((res)=>{
if(res.body.code==200){
let addressList= res.body.data.addressList;
this.name=addressList.address_name;
this.phone=addressList.mobile;
let address=[];
address[0]=addressList.province;
address[1]=addressList.city;
address[2]=addressList.district;
this.address=address;
this.addressDetils=addressList.address;
this.isDefault=addressList.is_set_default==1?true:false;
}
})
},
/* 新增地址 */
addAddress(){
/* 地址校验 */
if(!this.name){
this.confrim="请输入姓名";
this.toast=true;
return false;
}
if(!this.phone){
this.confrim="请输入联系电话";
this.toast=true;
return false;
}
if(!this.address[0]){
this.confrim="请选择地区";
this.toast=true;
return false;
}
if(!this.addressDetils){
this.confrim="请输入详细地址";
this.toast=true;
return false;
}
let addresslist=(this.getName (this.address)).split(" ");
if(this.title=="地址编辑"){
/* 编辑地址 */
API.person.addressEdit({
id:this.id,
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
address_name:this.name,
mobile:this.phone,
province:addresslist[0],
city:addresslist[1],
district:addresslist[2],
address:this.addressDetils,
postcode:null,
is_set_default:this.isDefault==true?1:0,
}).then((res)=>{
if(res.body.code==200){
this.confrim="修改成功";
this.success=true;
/* 添加成功后路由回退 */
setTimeout(() =>{
this.routerBack();
}, 1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}else{

/* 添加地址 */
API.person.addressAdd({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
address_name:this.name,
mobile:this.phone,
province:addresslist[0],
city:addresslist[1],
district:addresslist[2],
address:this.addressDetils,
postcode:null,
is_set_default:this.isDefault==true?1:0,
}).then((res)=>{
if(res.body.code==200){
this.confrim="添加成功";
this.success=true;
/* 添加成功后路由回退 */
setTimeout(() =>{
this.routerBack();
}, 1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}

}
},
mounted(){
this.typeCheck();
},
/* 非缓存插件 */
activated(){
this.typeCheck();
}
}



