
import HeaderCop from '../common/header.vue';
import BScroll from '../common/scrollView.vue';
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';

export default {
components: {
XImg,
LoadMore,
Actionsheet,
Scroller,
Masker,
Spinner,
XButton,
Group,
Cell,
Toast,
BScroll,
HeaderCop
},
data() {
return {
title: "我的订单",
haveData: false,//是否有数据
page: 1,
/* 上拉加载更多 */
swiper_pullUp: false,//显示加载
swiper_nodata: false,//没有更多数据
bookList: [],//数据列表
typeList: [
"", "", "待发货", "待确认收货", "待归还", "待结算", "", "待评价", "已完成", "订单取消"
],//状态列表
confrim: "",
toast: false,
scrollTop: 0,
onFetching: false,
page: 1,
orderId: null,
loading: false,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
mounted() {

},
activated() {
this.listRefresh();
},
methods: {
maxHeight(item) {
item.isHeight = !item.isHeight;
let data = this.bookList;
this.bookList = [];
this.bookList = data;
setTimeout(()=>{
this.scollRefresh();
},20);
},
/**
* 进入订单详情
*/
getInfo(id) {
this.$router.push({
path: '/book_bookOrderMessage/' + id
});
},
/**@argument
* 列表刷新
*/
listRefresh() {
this.swiper_nodata = false;
this.page = 1;
this.bookList = [];
this.getList();
},
/**@argument
* 获取我的计划列表
*/
getList() {
if (this.loading) {
return false;
}
this.loading = true;
if (this.bookList.length >= 10) {
this.swiper_pullUp = true;
this.swiper_nodata = false;
}
API.book.orderList({
page: this.page,
page_number: 10,
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
let time = 0;
if (this.bookList.length != 0) {
time = 500;
}
setTimeout(() => {
if (res.body.data.length > 0) {
this.swiper_pullUp = false;
this.bookList = this.bookList.concat(res.body.data);
this.page++;
} else {
this.swiper_pullUp = false;
if (this.bookList.length >= 10) {
this.swiper_nodata = true;
}
}
this.$nextTick(() => {
this.scollRefresh();
this.loading = false;
});

if (this.bookList.length == 0) {
this.haveData = true;
} else {
this.haveData = false;
}

}, time);
}
})
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
/* 删除订单 */
deletOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要删除该订单吗',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderDel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "删除成功";
self.toast = true;

self.listRefresh();
}
});
}
});

},
/**取消订单 */
cancelOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要取消该订单吗?',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderCancel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "取消成功";
self.toast = true;

self.listRefresh();
}
});
}
});
},
/* 确认收货 */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认收货',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderreceipt({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "确认收货成功";
self.toast = true;
/**@argument
* 此处进行删除后的重置工作
*/
self.listRefresh();
}
});
}
});
},
/* 确认归还 */
confirmReturn(item) {
/* * sinceId 自提点编号
* expressId 物流单号
* logisticsName 物流公司简拼
* revertId 归还地址编号
* company 物流公司名 */
let self = this;
this.$router.push({ path: '/orderReturn?orderId=' + item.order_id + '&from=book&book_id=' + item.books[0].book_id });
},
/* 提醒发货 ,结算 1-提醒发货2-提醒结算*/
remind(id, type) {
let self = this;
API.book.subOrderMsg({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "提醒发货成功";
} else {
self.confrim = "提醒结算成功";
}
self.toast = true;
}
});
},
/* 查看物流,评价, */
download(id) {
this.$router.push({
path: '/download'
});
}
}
}






