
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Toast from 'vux/src/components/toast/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../../services';
import HeaderCop from '../common/header.vue';
import BScroll from '../common/scrollView.vue';
export default {
components: {
XHeader,
Group,
Cell,
XTextarea,
Toast,
CellBox,
BScroll,
HeaderCop
},
data() {
return {
confrim: "请选择地址",
/* 提示信息 */
toast: false,
/* 是否拥有默认数据 */
haveDefault: false,
/* 订单详情数据 */
data: {
card:[
{
name:"小状元计划说明",
select:[],
child:[],
},
{
name:"小博士计划说明",
select:[],
child:[],
}
]
},
title: "结算",
zmScore:0
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone',
'getIsCertify'
]),
returnDesopte(){
let couter=0;
for (let item of this.data.card) {
for (const ite of item.child) {
if(ite.click==1){
couter+=ite.deposit-0;
}
}

}
return couter;
},
returnPrice(){
let couter=0;
for (let item of this.data.card) {
for (const ite of item.child) {
if(ite.click==1){
couter+=ite.price-0;
}
}

}
return couter;
},
returnTotal(){
if(this.zmScore==0){
return this.returnPrice+this.returnDesopte;
}else{
return this.returnPrice;
}
}
},
methods: {
itemChange(ite,index){
this.data.card[ite].child[index].click=this.data.card[ite].child[index].click||0;
this.data.card[ite].child[index].click=1-this.data.card[ite].child[index].click;
let currentData=this.data;
this.data={};
this.data=currentData;
},
/* 进入会员卡说明页面 */
enterInfo(item){
localStorage.setItem("cardInfo",JSON.stringify(
item
));
this.$router.push({
path:'/book_planInfo'
})
},
/* 芝麻信用授权 */
authorization(){
this.$router.push({
path:'/authInfo'
})
},
/**@argument
* 获取芝麻信用分
*/
userZMScore() {
API.person.getUserZMScore({
user_id: this.getUserInfoUserId,
zmtype:2//添加从绘本进入芝麻信用标记字段
}).then((res) => {

if (res.body.code == 200) {
if(res.body.msg == "获取成功"){
this.zmScore = res.body.data.zmscore;
}
}
})
},
/**@argument
* 选择框改变
*/
/**@argument
* 滚动列表重置刷新
*/
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
routerBack() {
this.$router.goBack();
},
/**@argument
* 提交订单
*/
buygoods() {
if(this.getIsCertify!=2&&this.getIsCertify!=4){
this.$router.push({
path:'/authInfo'
});
return false;
}
let resultList=[];
for (let item of this.data.card) {
for (const ite of item.child) {
if(ite.click==1){
resultList.push(ite);
}
}
}
if(resultList.length==0){
this.confrim = "您未选择任何计划";
this.toast = true;
return false;
}
if(resultList.length>4){
this.confrim = "最多只能购买4个计划";
this.toast = true;
return false;
}
localStorage.setItem('resultList',JSON.stringify(resultList));
this.$router.push({
path:'/book_bookCardInfo'
})
},
/**@argument
* 数据格式化
*/
dataForm(data){
let list=[
{
name:"",
select:[],
child:[],
},
{
name:"",
select:[],
child:[],
}
];
for (let item of data) {
if(item.rank==1){
list[0].child.push(item);
list[0].name=item.name;
} else{
list[1].child.push(item);
list[1].name=item.name;
}
}
this.data.card=list;
},
/**@argument
* 数据初始化
*/
Initialization() {
API.book.getCardList({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
this.data = res.body.data;
this.dataForm(this.data.card);
}else{
alert(JSON.stringify(res.body));
}
}).then(()=>{
this.userZMScore();
});
},


}, mounted() {
this.Initialization();
},
activated() {

}
}



