}
}).then((res)=>{
if(res.body.code == 200){
window.location.href=res.body.data.link_url;
}
},(err)=>{
}); */
}
});
} else {
this.payShow = true;
}

},
/* 确认收货 */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认收货',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.order.orderReceipt({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "确认收货成功";
self.orderList[this.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
self.toast = true;
}
});
}
});
},
/* 确认归还 */
confirmReturn(item) {
/* * sinceId 自提点编号
* expressId 物流单号
* logisticsName 物流公司简拼
* revertId 归还地址编号
* company 物流公司名 */
let self = this;
this.$router.push({ path: '/orderReturn?type=' + item.order_express_type + "&orderId=" + item.order_id });
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/* 确认结算 */
confrimSettlement(id) {
this.$router.push({
path: '/settlement', query: {
type: 1,
id: id,
}
});
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/* 查看结算单 */
seeSettlement(id) {
this.$router.push({
path: '/settlement/?id=' + id
});

localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
},
/* 提醒发货 ,结算 1-提醒发货2-提醒结算*/
remind(id, type) {
let self = this;
API.order.orderRemind({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderId: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "提醒发货成功";
} else {
self.confrim = "提醒结算成功";
}
self.toast = true;
}
});
},
/* 查看物流,评价, */
download(id) {
this.$router.push({
path: '/download'
});
localStorage.setItem("orderScroll", document.querySelector(".order_list").scrollTop);
}
}
}






