
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
ListCompent,
LoadMore
},
data () {
return {
canBottom:true,
loadshow:false,
haveData:false,
page:1,
goodsList:[1,2,6,4],
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods:{
routerBack(){
localStorage.setItem('collection','11');
localStorage.setItem("collentionTop",0);
this.$router.goBack();
},
/* 跳转到首页 */
goIndex(){
this.$router.push({
path: '/index/main'
});
},
/* 获取数据 */
getData(){
API.person.shopCollectList({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
page:this.page,
pageSize:10,
}).then((res)=>{
this.goodsList=res.body.data.shopList.data;
if(res.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
},
getMoreData(){
if(this.haveData){
this.loadshow=true;
API.person.shopCollectList({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
page:this.page,
pageSize:10,
}).then((res)=>{
setTimeout(()=>{
this.goodsList=this.goodsList.concat(res.body.data.shopList.data);
this.canBottom=true;
this.loadshow=false;
},500);
if(res.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}else{
this.canBottom=true;
}
}
},
mounted(){
/* 收据初始化 */
this.page=1;
this.haveData=true;
this.getData();
overscroll(document.querySelector('.collention_main'));
let self = this;
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (document.querySelector('.collention_main')) {
bodyScrollTop = document.querySelector('.collention_main').scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}

function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (document.querySelector('.collention_main')) {
bodyScrollHeight = document.querySelector('.collention_main').scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}

function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
document.querySelector('.collention_main').onscroll = function() {
if (getScrollTop() + getWindowHeight() >= (getScrollHeight()-10)) {
if(self.canBottom==true){
self.canBottom=false;
self.getMoreData();
}
}
};
},
activated(){
setTimeout(()=>{
document.querySelector(".collention_main").scrollTop=localStorage.getItem("collentionTop");
},50);
overscroll(document.querySelector('.collention_main'));
if(localStorage.getItem('collection')){
localStorage.setItem('collection','');
this.page=1;
this.haveData=true;
this.getData();
}

}
}



