
import HeaderCop from '../common/header.vue';
import BookList from '../common/bookList.vue';
import BScroll from '../common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
directives: {
TransferDom
},
components: {
BookList,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
BScroll,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data () {
return {
age:{
start:0,
end:0
},
iconList:[],//关键词列表
/* 上拉加载更多 */
title:"选书",
searchValue:"",//input选择框
slectNumber:0,
typeList:[],//类型列表
confrim:"",
toast:false,
isbook:false,
}
},
methods:{
/**
* 获取所有静态属性
*/
getTypes(){
API.book.searchCondition().then((res)=>{
if(res.body.code==200){
this.typeList=res.body.data;
}
})
},
typeClick(item,ite){
let haveClick=0;
for (const i of ite.data) {
if(i.click==1){
haveClick++;
}
}
/* 获取已经点击数 */
if(haveClick==ite.max_pick&&!item.click&&ite.max_pick>1){
this.confrim="最多只能选择"+ite.max_pick+"项";
this.toast=true;
return false;
}
item.click=item.click||0;
if(ite.max_pick==1){
if(item.click==0){
for (const it of ite.data) {
it.click=0;
}
item.click=1;
}else{
item.click=0;
}
}else{
/* 切换模式 */

item.click=1-item.click;
}




let cloneData=this.typeList;
this.typeList=[];
this.typeList=cloneData;
},
/**
* 重置参数
*/
resetParam(){
for (const item of this.typeList) {
for (const ite of item.data) {
ite.click=0;
}
}
this.searchValue="";
let cloneData=this.typeList;
this.typeList=[];
this.typeList=cloneData;
},
/**@argumexi
* 进入详情
*/
enterInfo(){
/**@argument
* 搜索参数初始化
*/
this.iconList=[];
let param={
common_params:[

],
title:this.searchValue,
order_method:0,
page_number:10,
user_id:this.getUserInfoUserId,
token:this.getUserInfoToken,
page :1
}


/* 智能选书选项内容拼接 */
/**双循环初始化数据 */
for (let index = 0; index < this.typeList.length; index++) {
param.common_params.push({
condition_id:this.typeList[index].condition_id,
checked_ids:[],
});
for (const ite of this.typeList[index].data) {
if(ite.click==1){
param.common_params[index].checked_ids.push(ite.param_id);
this.iconList.push(ite.name);
}
}
}


if(this.$route.fullPath=='/book_bookSearch'){
param.page_number=10;
param.common_params=JSON.stringify(param.common_params);
this.$router.push({
path:'/book_bookSearchRe',
query:{
param:JSON.stringify(param)
}
})
}else{
for (const item of param.common_params) {
if(item.condition_id==2){
if(item.checked_ids.length==0){
this.confrim="请选择图书本书";
this.toast=true;
return false;
}
param.page_number=item.checked_ids[0];
}
}
param.common_params=JSON.stringify(param.common_params);
this.$router.push({
path:'/book_bookPerSearchRe',
query:{
param:JSON.stringify(param),
icon: JSON.stringify(this.iconList)
}
})
}
}



},
mounted () {
this.getTypes();
},
activated () {
if(this.$route.fullPath=='/book_bookSearch'){
this.title="选书";
this.isbook=true;
}else{
this.title="智能选书";
this.isbook=false;
}

}
}



