
import HeaderCop from '../common/header.vue';
import BScroll from '../common/scrollView.vue';
import { API, getQuery } from '../../../services';
import { mapGetters } from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

export default {
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
BScroll,
Toast
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
title: "我的计划",
haveData: false,
isChoose:0,//是否处于选择状态
bookList: [{
"total_use_times": 12,
/* 总的可使用次数 */
"type": 4,
/* 类型：1-月卡2-季卡3-半年卡4-年卡 */
"card_id": 2,
/* card_id */
"month_use_times": 2,
/* 当月可使用次数 */
"total_remain": 12,
/* 总的剩余使用次数 */
"one_rent_num": 8,
/* 一次可借的书本数量 */
"card_no": "1234567890",
/* 卡号 */
"price": "599.00",
/* price */
"name": "小博士计划",
/* name */
"invalid_time": 1888888888,
/* 失效时间 */
"rank": 2,
/* 等级：1-金卡(状元计划)2-银卡(博士计划)3-普通卡 */
"month_remain": 1,
/* 当月剩余使用次数 */
"effect_time": 0,
/* 生效时间 */
"status": 1
/* 状态1-有效 2-无效 3-冻结*/
}],
confrim: "",
toast: false,
}
},
methods: {
routerBack() {
this.$router.goBack();
},
/**@argument
* 选择计划
*/
choosePlan(item){
if(this.isChoose==1){
let name=item.name;
localStorage.setItem('choosePlan', JSON.stringify({
name:name,
id:item.user_card_id
}));
this.routerBack();
}else{
localStorage.setItem("cardInfo",JSON.stringify({
child:[item]
}
));
this.$router.push({
path:'/book_planInfo'
})
}
},
getUserData(num){
API.book.myValidCards({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_num:num
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;
if(this.bookList.length==0){
this.haveData=true;
}else{
this.haveData=false;
}
}
});
},
/**@argument
* 获取我的计划列表
*/
getData() {
API.book.getPlan({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;
if(this.bookList.length==0){
this.haveData=true;
}else{
this.haveData=false;
}
}
})
},
/* 滚动列表重置刷新 */
scollRefresh(){
this.$refs.scollView.scroll.refresh();
},
/* 支付宝付款成功后的回调函数 */
success(item) {
let self=this;
API.alipay.success({
out_order_no: item.out_order_no,
order_no: item.order_no,
invoke_state: item.invoke_state,
user_id: item.user_id,
admit_state: item.admit_state,
}).then((res) => {
/* alert("借还注入") */
if (res.body.code == 200) {
/* alert("进入支付"); */
API.book.H5Pay({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
orderSn: JSON.parse(item.invoke_state).order_sn,
payMethod: 4,
openId: 0,
}).then((resopndy) => {
/* alert("调动支付"); */
if (resopndy.body.code == 250) {
self.confrim = "支付完成";
self.toast = true;
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
} else {
const div = document.createElement('div');
div.innerHTML = resopndy.body;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
},(err)=>{
/* alert("支付请求失败"); */
alert(JSON.stringify(err));
});
}else{
alert(res.body.msg);
}
}, (err) => {
alert("借还注入错误");
alert(JSON.stringify(err));
});
},
},

mounted() {

},
activated () {
this.isChoose=this.$route.query.choose||0;
if(this.isChoose==1){
this.title="请选择可用计划";
this.getUserData(this.$route.query.num);
}else{
this.title="我的计划";
this.getData();
}
/* 获取当前url参数 */
let biz_content = this.$route.query.biz_content;
/* 支付宝借还成功后的回调 */
if (biz_content) {
biz_content = JSON.parse(biz_content);
let timetamp = JSON.parse(biz_content.invoke_state).timestamp;
/* if (((new Date().getTime() / 1000) - timetamp) <= 30) { */
this.success(biz_content);

}
}
}



