
import HeaderCop from '../common/header.vue';
import BScroll from '../common/scrollView.vue';
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Masker from 'vux/src/components/masker/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';

export default {
components: {
XImg,
Actionsheet,
Group,
Cell,
Toast,
BScroll,
HeaderCop,
XTextarea
},
data() {
return {
title: "我的订单",
haveData: false,//是否有数据
confrim: "",
bookList: {
status:0,
books:[]
},//数据列表
typeList:[
"","","待发货","待确认收货","待归还","待结算","","待评价","已完成","取消"
],//状态列表
titleArray: [
{},{},
{
title: "等待商家发货",//待发货
message: ""
},
{
title: "商家已发货",//待收货
message: "请及时确认收货"
},
{
title: "待归还",//待归还
message: ""
},
{
title: "待结算中",//待结算
message: "等待商家确认收货并出具结算单"
},
{},
{
title: "订单待评价",//待评价
message: ""
},
{
title: "订单已完成",//评价完成
message: ""
},
{
title: "订单已取消",//订单关闭
message: ""
}
],
toast: false,
scrollTop: 0,
page: 1,
orderId: null,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
mounted() {

},
activated() {
/* 获取orderId */
this.orderId=this.$route.params.id;
this.getList();
},
methods: {
maxHeight(item){
item.isHeight=!item.isHeight;
let data=this.bookList;
this.bookList=[];
this.bookList=data;
setTimeout(()=>{
this.scollRefresh();
},20);
},
/**
* 进入订单详情
*/
getInfo(id) {
this.$router.push({
path: '/book_bookOrderMessage/' + id
});
},
/**@argument
* 列表刷新
*/
listRefresh() {
this.page = 1;
this.bookList = [];
this.getList();
},
/**@argument
* 获取我的计划列表
*/
getList() {
API.book.orderDetail({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
order_id:this.orderId
}).then((res) => {
if(res.body.code==200){
this.bookList=res.body.data;

}
})
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
/* 删除订单 */
deletOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要删除该订单吗',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderComment({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.loading = true;
self.confrim = "删除成功";
self.toast = true;
/**@argument
* 此处进行删除后的重置工作
*/
self.getList();
}
});
}
});

},
/**取消订单 */
cancelOrder(id){
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要取消该订单吗?',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderCancel({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.loading = true;
self.confrim = "取消成功";
self.toast = true;
/**@argument
* 此处进行删除后的重置工作
*/
self.getList();
}
});
}
});
},
/* 确认收货 */
confrimOrder(id) {
let self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认收货',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.book.orderreceipt({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
}).then((res) => {
if (res.body.code == 200) {
self.confrim = "确认收货成功";
self.toast = true;
/**@argument
* 此处进行删除后的重置工作
*/
self.getList();
}
});
}
});
},
/* 确认归还 */
confirmReturn(item) {
/* * sinceId 自提点编号
* expressId 物流单号
* logisticsName 物流公司简拼
* revertId 归还地址编号
* company 物流公司名 */
let self = this;
this.$router.push({ path: '/orderReturn?orderId=' + item.order_id + '&from=book&book_id=' + item.books[0].book_id });
},
/* 提醒发货 ,结算 1-提醒发货2-提醒结算*/
remind(id, type) {
let self = this;
API.book.subOrderMsg({
user_id: self.getUserInfoUserId,
token: self.getUserInfoToken,
order_id: id,
type: type,
}).then((res) => {
if (res.body.code == 200) {
if (type == 1) {
self.confrim = "提醒发货成功";
} else {
self.confrim = "提醒结算成功";
}
self.toast = true;
}
});
},
/* 查看物流,评价, */
download(id) {
this.$router.push({
path: '/download'
});
}
}
}






