
import ListCompent from '../list/listCompent.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from 'vuex'
import {API,getQuery} from '../../services'

export default {
components: {
XHeader,
Toast,
LoadMore,
ListCompent
},
data () {
return {
haveData:true,
page:1,
canBottom:true,
loadshow:false,
confrim:"请选择地址",
toast:false,
storeName:null,
storeFace:"",
storebgPic:"",
storeId:null,
goodsList:[1,5,7,6,8],
cardList:[]
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
},
methods:{
routerBack(){
this.$router.goBack();
},
/* 领取店铺优惠券 */
getCard(id){
if(!this.getUserInfoUserId){
this.$router.push({
path:"/login",
});
return false;
}
API.card.receiveStoreCoupon({
storeId:this.storeId,
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
couponNo:id
}).then((res)=>{
if(res.body.code==200){
this.confrim="领取成功";
this.toast=true;
}else{
this.confrim=res.body.msg;
this.toast=true;
}
})
},
/* 跳到店铺详情页面 */
goInfo(){
this.$router.push({
path:'/shopInfo/'+this.storeId
})

},
/* 数据初始化 */
Initialization(){
this.storeId=this.$route.params.id;
API.main.storeInfo({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.storeName=res.body.data.storeName;
this.storeFace=res.body.data.storeFace;
this.storebgPic=res.body.data.storebgPic;
}
});
API.main.storeGoods({
storeId:this.storeId,
page:this.page,
}).then((res)=>{
if(res.body.code==200){
this.goodsList=res.body.data.shopList.data;
if(res.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
}
});
/**
* 获取店铺红包
*/
API.card.stordCardList({
storeId:this.storeId
}).then((res)=>{
if(res.body.code==200){
this.cardList=res.body.data;
}
})
},
/* 获取更多数据 */
getMoreData(){
if(this.haveData){
this.loadshow=true;
API.main.storeGoods({
storeId:this.storeId,
page_number:10,
page:this.page,
}).then((Response)=>{
setTimeout(()=>{
this.goodsList=this.goodsList.concat(Response.body.data.shopList.data);
this.canBottom=true;
this.loadshow=false;
},500);
if(Response.body.data.shopList.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
});
}else{
this.canBottom=true;
}
}
},
mounted(){
overscroll(document.querySelector('.shop_main_cardList'));
overscroll(document.querySelector('.shop_main_goodsList'));
let self = this;
let mainbox = document.querySelector(".shop_main_goodsList");
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (mainbox) {
bodyScrollTop = mainbox.scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}
function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (mainbox) {
bodyScrollHeight = mainbox.scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}
function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
document.querySelector(".shop_main_goodsList").onscroll = function() {
if ((getScrollTop() + getWindowHeight()) >= (getScrollHeight() - 10)) {
if (self.canBottom == true) {
self.canBottom = false;
self.getMoreData();
}
}
};
this.Initialization();
},
activated(){
overscroll(document.querySelector('.shop_main_cardList'));
overscroll(document.querySelector('.shop_main_goodsList'));
if(localStorage.getItem("store")){
this.page=1;
this.canBottom=true;
this.haveData=true;
this.Initialization();
window.localStorage.setItem("store",'');
}else{
setTimeout(() => {
document.querySelector(".shop_main_goodsList").scrollTop = localStorage.getItem("storeTop");
}, 50);
}

}
}



