
import BookList from './common/bookList.vue';
import BScroll from './common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../services';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
directives: {
TransferDom
},
components: {
BookList,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
BScroll
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
/* 上拉加载更多 */
swiper_pullUp: false,//显示加载
swiper_nodata: false,//没有更多数据
bookList: [],
title: "我的收藏",
page: 1,
noData: false,
loading: false,
}
}, methods: {
/**@argument
* 列表刷新
*/
listRefresh() {
this.page = 1;
this.getList();
},
/**@argument
* 获取列表数据
*/
getList() {
if (this.loading) {
return false;
}
this.loading = true;
if (this.bookList.length >= 10) {
this.swiper_pullUp = true;
this.swiper_nodata = false;
}
API.book.getColl({
page: this.page,
page_number: 10,
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
start_date: window.startTime
}).then((res) => {
if (res.body.code == 200) {
let time = 0;
if (this.bookList.length != 0) {
time = 1500;
}

setTimeout(() => {
if (res.body.data.length > 0) {
this.swiper_pullUp = false;
if(this.page==1){
this.bookList=[];
}
this.bookList = this.bookList.concat(res.body.data);
this.page++;
} else {
this.swiper_pullUp = false;
if (this.bookList.length >= 6) {
this.swiper_nodata = true;
}
}
this.$nextTick(() => {
this.scollRefresh();
this.loading=false;
});

if (this.bookList.length == 0) {
this.noData = true;
} else {
this.noData = false;
}

}, time);
}
})
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},

},
watch: {
"bookList":function(item){
if(item.length==0){
this.noData=true;
}else{
this.noData=false;
}
}
},
mounted() {

},
activated() {
if(window.stopReload){
setTimeout(()=>{
console.log(localStorage.getItem("bookScroll"));
this.$refs.scollView.scroll.scrollTo(0,localStorage.getItem("bookScroll")-0,10);
},20);
window.stopReload=false;
}else{
this.listRefresh();}
}
}



