
import HeaderCop from '../common/header.vue';
import { API, getQuery } from '../../../services';
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'

export default {
directives: {
TransferDom
},
components: {
PopupRadio,
XHeader,
Actionsheet,
ButtonTab,
ButtonTabItem,
HeaderCop,
Group,
Datetime,
XTextarea
},
computed: {

},
data() {
return {
currentIndex: 0,
title: "基本设置",
babySetting: {
value: "",
name: "",
sex: ""
},
time: "2017-01-01",
star: "摩羯",
endDate: "2017-01-01",
sexList: ['男', '女'],
starList: [{
name: "魔羯",
title: "&#xe67a;"
}, {
name: "水瓶",
title: "&#xe673;"
}, {
name: "双鱼",
title: "&#xe676;"
}, {
name: "牡羊",
title: "&#xe679;"
}, {
name: "金牛",
title: "&#xe66f;"
}, {
name: "双子",
title: "&#xe674;"
}, {
name: "巨蟹",
title: "&#xe67b;"
}, {
name: "狮子",
title: "&#xe677;"
}, {
name: "处女",
title: "&#xe670;"
}, {
name: "天秤",
title: "&#xe672;"
}, {
name: "天蝎",
title: "&#xe671;"
}, {
name: "射手",
title: "&#xe678;"
},]

}
},
methods: {
/**@argument
* 生日更改时
*/
onChange() {
let m = this.babySetting.value.split("-")[1] - 0;
let d = this.babySetting.value.split("-")[2] - 0;
this.star = "魔羯水瓶双鱼牡羊金牛双子巨蟹狮子处女天秤天蝎射手魔羯".substr(m * 2 - (d < "102223444433".charAt(m - 1) - -19) * 2, 2);
this.babySetting.value = this.time;
localStorage.setItem("babySetting", JSON.stringify(this.babySetting));
this.getIndex();
},
getIndex() {
for (let item = 0; item < this.starList.length; item++) {
if (this.star == this.starList[item].name) {
this.currentIndex = item;
}
}
},
Change() {
localStorage.setItem("babySetting", JSON.stringify(this.babySetting));
this.getIndex();
}
},
mounted() {
if (localStorage.getItem("babySetting")) {
this.babySetting = JSON.parse(localStorage.getItem("babySetting"));
}
this.getIndex();
this.endDate = this.dateFormat(new Date().getTime(), 'YYYY-MM-DD');
setTimeout(() => {
this.time = this.babySetting.value;
}, 100)
}
}



