
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { mapGetters } from 'vuex';
import {API,getQuery} from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
XTextarea
},
data() {
return {
/* 时间对照表 */
timeMap:{1:"日",2:"周",3:"月",4:"季",5:"年"},
infoData:{},
type:"",
orderId:0,
}
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
timeText(){
return this.timeMap[this.infoData.rent_period_type];
},

},
methods: {
routerBack() {
this.$router.goBack();
},
getdata(){
API.order.orderSettleInfo({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
orderId:this.orderId,
}).then((res)=>{
if(res.body.code==200){
this.infoData=res.body.data.bill;
}
})
},
/* 确认结算 */
confrimSettlement(){
let self=this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '是否确认结算',
onConfirm () {
API.order.orderSettle({
userId:self.getUserInfoUserId,
token:self.getUserInfoToken,
orderId:self.orderId,
}).then((res)=>{
if(res.body.code==200){
self.confrim="结算成功";
self.toast=true;
localStorage.setItem("reload", "1");
setTimeout(()=>{
self.routerBack();
},500);
}
});
}
});
},
},
activated(){
overscroll(document.querySelector('.settlement_main'));
this.orderId=this.$route.query.id;
this.type=this.$route.query.type;
this.getdata();
},
mounted(){
overscroll(document.querySelector('.settlement_main'));
}
}



