
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {API,getQuery} from '../../services'
export default {
data() {
return {
/* 警告信息 */
confrim:"",
toast:false,
state:true,
/* 用户手机号，验证码，新密码 */
form:{
user_phone:"",
code:"",
user_password:""
},
/* 验证码内容 */
codeText:"获取验证码",
isDisable:false,
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Toast
},
mounted : function() {

},
methods :{
routerback(){
this.$router.goBack();
},
/* 获取验证码 */
getcode(){
/* 判断手机号是否有值 */
if(!this.form.user_phone){
this.confrim="请输入手机号码";
this.toast=true;
return false;
}
if(!this.isDisable){
API.login.sendCode({
user_phone:this.form.user_phone,
type:"back"
}).then((res)=>{
if(res.body.code==200){
this.isDisable=true;
this.codeText=60;
let inter=setInterval(()=>{
this.codeText--;
if(this.codeText==1){
clearInterval(inter);
this.isDisable=false;
this.codeText="获取验证码";
}
},1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}
},
/* 密码修改 */
changePassword(){
/* 值判断 */
if(!this.form.user_phone){
this.confrim="请输入手机号码";
this.toast=true;
return false;
}
if(!this.form.code){
this.confrim="请输入验证码";
this.toast=true;
return false;
}
if(!this.form.user_password){
this.confrim="请输入新密码";
this.toast=true;
return false;
}
API.login.updatePwd(this.form).then((res)=>{
if(res.body.code==200){
this.$router.push({
path:'/login',
})
}else{
this.confrim=res.body.msg;
this.toast=true;
}
},(res)=>{
this.confrim="操作失败";
this.toast=true;
});
},
}
}



