
import BScroll from '../common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'

export default {
components: {
Group,
Cell,
CellFormPreview,
CellBox,
BScroll,
Swiper,
SwiperItem,
},
data () {
return {
id:"",//图书id
data:"",
commentList:[],
haveMoreComment:false,

}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
methods:{
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
showAll(){
this.haveMoreComment=false;
setTimeout((res)=> {
this.scollRefresh();
}, 20);
},
/* 路由回退 */
routerBack() {
this.$router.goBack();
},
/**@argument
* 加入购物车
*/
addCar(item){
if(!this.getUserInfoUserId){
this.$router.push({
path:"/login"
});
return false;
}
if(item.is_shelf==0){
API.book.addCar({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_ids:[item.book_id]
}).then((res) => {
if (res.body.code == 200) {
item.is_shelf=1;
this.$vux.toast.show({
text: '已加入借书架'
});
}else if(res.body.code == 250){
this.$router.push({
path:"/book_bookCard"
})
}
})
}else{

}

},
/**@argument
* 添加收藏
*/
addColl(item){
if(!this.getUserInfoUserId){
this.$router.push({
path:"/login"
})
return false;
}
if(item.is_collected==0){
API.book.addColl({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_ids:[item.book_id]
}).then((res) => {
if (res.body.code == 200) {
item.is_collected=1;
this.$vux.toast.show({
text: '收藏成功'
});
}
})
}else{
API.book.cancelColl({
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
book_ids:[item.book_id]
}).then((res) => {
if (res.body.code == 200) {
item.is_collected=0;
this.$vux.toast.show({
text: '取消收藏'
});
}
})
}
},
/**@argument
* 进入借书架
*/
bookCar(){
this.$router.push({
path:'/book/main/car'
})
},
/**@argument
* 获取基本数据
*/
getDefault(id){
API.book.getBookDetail({
book_id:id,
user_id: this.getUserInfoUserId,
token: this.getUserInfoToken,
start_date:window.startTime
}).then((res)=>{
if(res.body.code==200){
this.data=res.body.data;
}
}).then(()=>{
this.getComment();
});
},
/**@argument
* 获取评论列表
*/
getComment(){
API.book.getBookComment({
book_id:this.id,
}).then((res)=>{
if(res.body.code==200){
/**@argument
* 字符串截取1-5;
*/
if(res.body.data.length>5){
this.haveMoreComment=true;
}
this.commentList=res.body.data;
}
});
}
},
activated () {
this.id=this.$route.params.id;
this.getDefault(this.id);
let domOBJ=document.querySelector(".wapper_content");
domOBJ.style.transform="translate(0px, 0px) translateZ(0px)";
}

}



