

import XImg from 'vux/src/components/x-img/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

import BScroll from '../common/scrollView.vue';
import { mapGetters } from 'vuex';
import { API, getQuery } from '../../../services';
export default {
components: {
XHeader,
XImg,
BScroll,
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
]),
},
data() {
return {
list: [

],
title: "绘本精选",
id: "",
}
},
activated() {
this.id = this.$route.query.id;
this.getData();
},
mounted() {

},
methods: {
goSeriesList(type,series,name){
if(type==1){
this.$router.push({
path:"/book_bookSeriesList",
query:{
series:series,
name:name
}
})
}else{
let param={
common_params:[

],
title:"",
order_method:0,
page_number:10,
user_id:this.getUserInfoUserId,
token:this.getUserInfoToken,
page :1
}
param.common_params=series;
this.$router.push({
path:'/book_bookSearchRe',
query:{
param:JSON.stringify(param)
}
})
}

},
getData() {
API.book.getFeatured({
channel_id: this.id
}).then((res) => {
if (res.body.code == 200) {
this.list=res.body.data;
}
});
},
/* 滚动列表重置刷新 */
scollRefresh() {
this.$refs.scollView.scroll.refresh();
},
more() {
this.$router.push('/book/main')
},
routerBack() {
this.$router.goBack();
},


}
}



