
import ListCompent from '../list/listCompent.vue';
import Scroller from 'vux/src/components/scroller/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

import { API, getQuery } from '../../services';
import { mapGetters } from 'vuex';


export default {
components: {
XImg,
Masker,
Scroller,
Spinner,
XButton,
Group,
Cell,
LoadMore,
Swiper,
SwiperItem,
ListCompent
},
data() {
return {
cardUrl: "",
/* 红包内容 */
cardContent: {
price: 10,
text: "",
},
/* 新人红包是否显示 */
cardShow: false,
/*是否可以进行下拉加载 */
canBottom: true,
loadshow: false,
swiperItemIndex: 0,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
/* 轮播图数据集合 */
bannerlist: [

],
cardobj:{
title:"新人大礼包",
price:"30",
list:[
{
amount:"10",
title:"全场通用",
type:"新人专享",
},
{
amount:"20",
title:"全场通用",
type:"新人专享",
},
]
},
/* 时间周期对照表 */
timeMap: { 1: "日", 2: "周", 3: "月", 4: "季", 5: "年" },
/* 当前被选中列表 */
currentType: 0,
/* 分类数据集合 */
typeList: [],
/* 首页热租商品 */
hotGoodsList: [],
/* 首页频道列表 */
channelList:[],
/* 首页标签 */
iconList:[],
/* 新品特惠商品列表 */
newGoodsList:[],
/* 首页推荐商品 */
recGoodsList: [],
/* 根据首页商品标签页长度进行商品数据列表缓存，防止重复请求数据 */
goodsList: [],
/* 当前被选中状态的商品信息列表 */
currentGoods: [],
}
},
activated() {
if(sessionStorage.getItem("mainReload")){
window.location.href="/#/index/main";
sessionStorage.setItem("mainReload",'');
}
overscroll(document.querySelector('.main_typelist_box'));
overscroll(document.querySelector('.main_listbox'));
overscroll(document.querySelector('.list_compent_list_box'));
setTimeout(() => {
document.querySelector(".main_listbox").scrollTop = localStorage.getItem("scrolltop");
document.querySelector(".list_compent_list_box").scrollTop = localStorage.getItem("scrolltop");
}, 50);
},
computed: {
iswexin(){
var ua = window.navigator.userAgent.toLowerCase();
if (ua.match(/MicroMessenger/i) == 'micromessenger') {
return true;
} else {
return false;
}
},
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getIsCertify',
'getUserInfoUserId'
]),
isIOS(){
var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
/*  */
if(isiOS){
return true;
}else{
return false;
}
}
},
methods: {
/* 频道点击函数 */
channelClick(name,id){
let scrollTop = document.querySelector(".main_listbox").scrollTop;
localStorage.setItem("scrolltop", scrollTop);
this.$router.push({
path:'/chaneel?name='+name+"&id="+id,
})
},
/* 功能table点击函数 */
funClick(name,type,id=1,url=""){
let scrollTop = document.querySelector(".main_listbox").scrollTop;
localStorage.setItem("scrolltop", scrollTop);
if(type==1){
this.$router.push({
path:'/moreHot/?name='+name+'&id='+id+'&type=icon'
})
}else if(type==2){
this.$router.push({
path:'/selected/?id='+id
})
}else{
if(name=="分享有礼"){
window.location.href="/down.html";
}else if(name=="绘本"){
this.$router.push({
path:'/book',
})
}

}
},
/* 是否显示活动红包 */
isShowCard() {
API.card.getCouponActive().then((res) => {
if (res.body.code == 200) {
this.smallCard = true;
this.cardUrl = res.body.data.url;
}
})
},
/* 获取红包 */
getCard() {
API.card.remindCard({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
}).then((res) => {
if (res.body.code == 200) {
this.cardobj.list=res.body.data
this.cardShow = true;
}
});
},
gouese(){
this.cardShow=false;
this.$router.push({
path: '/card'
});
},
/* 红包点击 */
cardClick() {
window.location.href = this.cardUrl;
},
closeCard() {
this.cardShow = false;
},
onSwiperItemIndexChange() {

},
/* 进入商品详情 */
goInfo(id) {
if (document.querySelector(".main_listbox")) {
let scrollTop = document.querySelector(".main_listbox").scrollTop;
localStorage.setItem("scrolltop", scrollTop);
}
localStorage.setItem("goodsInfo", "11");
this.$router.push({
path: '/goodsInfo/' + id
});

},
/* 列表点击切换函数 */
/* 根据焦点位置判断 */
typeselect(index, id) {
let scrollTop = document.querySelector(".main_listbox").scrollTop;
localStorage.setItem("scrolltop", scrollTop);
for (let item of this.typeList) {
item.click = false;
}
/* 传入对应的商品信息id */
this.currentType = index;
this.typeList[index].click = true;
var data = this.typeList;
this.typeList = null;
this.typeList = data;
this.getData(id, index);
},
/* 列表滚动选择函数 */
onScroll(pos) {
this.scrollTop = pos.top
},
/* 根据列表id获取当前列表对应数据 */
getData(id, index) {

document.querySelector(".main_listbox").scrollTop = 0;
if (this.goodsList[index] || index == 0) {
this.currentGoods = this.goodsList[index];
return false;
}
API.main.searchGoodsCategory({
chooseId: id,
page: 1,
}).then((Response) => {
this.goodsList[index] = Response.body.data.shopList.data;
document.querySelector(".list_compent_list_box").scrollTop = 100;
document.querySelector(".list_compent_list_box").scrollTop = 0;
if (Response.body.data.shopList.data.length == 10) {
this.typeList[index].havedata = true;
} else {
this.typeList[index].havedata = false;
}
this.currentGoods = this.goodsList[index];
});
},
/* 分页获取更多数据 */
getMoreData() {
if (this.currentType == 0 || !this.typeList[this.currentType].havedata) {
this.canBottom = true;
return false;
} else {
this.loadshow = true;
API.main.searchGoodsCategory({
chooseId: this.typeList[this.currentType].goods_category_id,
page: this.goodsList[this.currentType].length / 10 + 1
}).then((Response) => {
setTimeout(() => {
this.loadshow = false;
this.goodsList[this.currentType] = this.goodsList[this.currentType].concat(Response.body.data.shopList.data);
this.currentGoods = this.goodsList[this.currentType];
this.canBottom = true;
}, 500);
if (Response.body.data.shopList.data.length == 10) {
this.typeList[this.currentType].havedata = true;
} else {
this.typeList[this.currentType].havedata = false;
}
});
}

},
/* 列表页跳转 */
golist() {
this.$router.push({
path: '/list'
});

},
/* 跳转到搜索页面 */
gosearch() {
this.$router.push({
path: '/search'
});
},
/* 跳转多更多商品页面 */
getMoreHot() {
this.$router.push({
path: '/moreHot'
});

},
/* 轮播图点击函数 */
bannerClick(id, type) {
/* 1：店铺 2：商品 3：url */
if (type == 1) {
this.$router.push({
path: '/shop/' + id
});
} else if (type == 2) {
localStorage.setItem("goodsInfo", "11");
this.$router.push({
path: '/goodsInfo/' + id
});
} else {
if(id.indexOf('wap')>-1){
id=id.replace("wap","m");
}
window.location.href = id+'?type=H5';
}
},
},
mounted() {
/* 可拖动返回列表滚动初始化 */
overscroll(document.querySelector('.main_typelist_box'));
overscroll(document.querySelector('.main_listbox'));
overscroll(document.querySelector('.list_compent_list_box'));
/* 获取轮播图信息 */
API.main.getBanner({
}).then((Response) => {
this.bannerlist = Response.body.data.bannerList;
});
/* 获取频道信息 */
API.newMain.channelList().then((res)=>{
if(res.body.code==200){
this.channelList=res.body.data;
}
});
/* 获取活动标签信息 */
API.newMain.iconList().then((res)=>{
if(res.body.code==200){
this.iconList=res.body.data;
}
});
/* 获取新品特惠信息 */
API.newMain.newGoods().then((res)=>{
if(res.body.code==200){
this.newGoodsList=res.body.data.data;
}
})
/* 获取首页标签 */
API.main.hotTagLabel().then((Response) => {
let firstLabel = {
hot_label_name: "精选",
click: true
}
let shopList = Response.body.data.shopList;
for (let item of shopList) {
item.click = false;
}
shopList.splice(0, 0, firstLabel);
this.goodsList = new Array(shopList.length);
this.typeList = shopList;
});
/* 获取热门商品 */
API.main.goodsHot({
recomandId: 3,
page_number: 3
}).then((Response) => {
this.hotGoodsList = Response.body.data.shopList.data;
});
/* 获取新人红包 */
this.isShowCard();
this.getCard();
/* 获取推荐商品 */
API.main.goodsIndexRecom({
page_number: 30,
}).then((Response) => {
this.recGoodsList = Response.body.data.shopList.data;
});
/* 下拉加载更多操作 */
let self = this;
let mainbox = document.querySelector(".list_compent_list_box");
resizeScroll(mainbox,self,self.getMoreData,104);

}
}



