

import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from 'vuex'
import { API, getQuery } from '../../services';

export default {
components: {
XHeader,
Group,
Cell,
Confirm,
Toast,
LoadMore
},
data() {
return {
canBottom: true,
loadshow: false,
haveData: true,
confrim: "删除成功",
toast: false,
/* 默认选中index */
currentIndex: 0,
/* 当前列表index 集合 */
valList: [],
/* 地址列表 */
addressList: {
data: []
},
currentPage: 1,
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods: {
routerBack() {
this.$router.goBack();
},
/* 默认地址选中项更改 */
checkDefault(id, index) {
API.person.addressDefault({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
id: id
}).then((res) => {
if (res.body.code == 200) {
this.addressList.data[this.currentIndex].is_set_default = 0;
this.addressList.data[index].is_set_default == 1 ? this.addressList.data[index].is_set_default = 0 : this.addressList.data[index].is_set_default = 1;
this.currentIndex = index;
}
})
},
/* 地址编辑 */
editAddress(id) {
/* 进入地址编辑页面 */
this.$router.push({
path:'/editAddress/'+id,
})
},
/* 地址删除 */
deletAddress(id, index) {
var self = this;
this.$vux.confirm.show({
/* title: 'Title', */
content: '确定要删除该地址吗？',
onConfirm() {
/* 点击确认时执行具体删除操作 */
API.person.addressDelete({
userId: self.getUserInfoUserId,
token: self.getUserInfoToken,
id: id
}).then((res) => {
if (res.body.code == 200) {
self.confirm = "删除成功";
self.toast = true;
self.addressList.data.splice(index, 1);
}
})
}
})
},
/* 获取用户地址列表 */
getAddress() {
if (this.haveData) {
this.loadshow = true;
/* 获取默认地址数据 */
API.person.getAddressList({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
pageSize: 10,
page: this.currentPage,
}).then((res) => {
if (res.body.code == 200) {
if (this.currentPage == 1) {
this.addressList = res.body.data.addressList;
if (res.body.data.addressList.data.length == 10) {
this.haveData = true;
this.currentPage++;
} else {
this.haveData = false;
}
this.canBottom = true;
this.loadshow = false;
} else {
this.addressList.data = this.addressList.data.concat(res.body.data.addressList.data);
let checkValue = new Array(this.addressList.data.length);
for (let i = 0; i < this.addressList.data.length; i++) {
let result = false;
if (this.addressList.data[i].is_set_default === 1) {
this.currentIndex = i;
result = true;
}
checkValue[i] = result;
this.valList.push(result);
}
this.canBottom = true;
this.loadshow = false;
if (res.body.data.addressList.data.length == 10) {
this.haveData = true;
this.currentPage++;
} else {
this.haveData = false;
}
}

}
});
} else {
this.canBottom = false;
}

},
/* 用户列表点击选择 */
chooseAddress(res) {
if (this.$route.query.chose == 1) {
this.$store.dispatch('CurrentTpl', 0);
this.$store.dispatch('SetAddress', res);
localStorage.setItem('addressClick','11');
/* 路由回退 */
this.routerBack();
}
}
},
mounted() {
overscroll(document.querySelector('.addressList_main'));
let self = this;
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (document.body) {
bodyScrollTop = document.body.scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}

function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (document.body) {
bodyScrollHeight = document.body.scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}

function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
window.onscroll = function() {
if (getScrollTop() + getWindowHeight() >= (getScrollHeight()-10)) {
if (self.canBottom == true) {
self.canBottom = false;
self.getAddress();
}
}
};

},
activated() {
overscroll(document.querySelector('.addressList_main'));
this.currentPage = 1;
this.canBottom = true;
this.haveData = true;
this.getAddress();
}
}



