
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {API,getQuery} from '../../services'
export default {
data() {
return {
confrim:"",
toast:false,
state:true,
type:"",
form:{
user_phone:"",
code:"",
},
/* 验证码内容 */
codeText:"获取验证码",
isDisable:false,
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Toast
},
mounted : function() {
},
methods :{
routerback(){
this.$router.goBack();
},
/* 获取验证码 */
getcode(){
/* 判断手机号是否有值 */
if(!this.form.user_phone){
this.confrim="请输入手机号码";
this.toast=true;
return false;
}
if(!this.isDisable){
API.login.sendCode({
user_phone:this.form.user_phone,
type:"logo"
}).then((res)=>{
if(res.body.code==200){
this.isDisable=true;
this.codeText=60;
let inter=setInterval(()=>{
this.codeText--;
if(this.codeText==1){
clearInterval(inter);
this.isDisable=false;
this.codeText="获取验证码";
}
},1000);
}else{
this.confrim=res.body.msg;
this.toast=true;
}
});
}
},
/* 登录 */
login(){
/* 值判断 */
if(!this.form.user_phone){
this.confrim="请输入手机号码";
this.toast=true;
return false;
}
if(!this.form.code){
this.confrim="请输入验证码";
this.toast=true;
return false;
}
API.login.codeLogin(this.form).then((Response)=>{
if(Response.body.code==200){
Response=Response.body.data;
/* 触发vuex登录状态更改操作 */
let token=this.md5(Response.user_id)+Response.user_id;
let userInfo={
loginname : Response.nickname,
avatar : Response.face,
id : Response.user_id,
token : token,
};
localStorage.setItem("userInfo",JSON.stringify(userInfo));
localStorage.setItem("isCertify",Response.isCertify);
this.$store.dispatch('IsCertify');
this.$store.dispatch('SetUserInfo',userInfo);
if(!this.type){
this.$router.push({
path:'/index/main'
})
}else{
localStorage.setItem("goodsInfo","login");
window.history.go(-2)
}
}else{
this.confrim=Response.body.msg;
this.toast=true;
}
});
},
regist(){
this.$router.push({
path:'/regist'
});
}
},
activated(){
this.type=this.$route.query.type;
}
}



