
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {API,getQuery} from '../../services';

export default {
data() {
return {
confrim:"",
toast:false,
state:true,
logincode:"/logincode",
type:"",
form:{
user_phone:"",
user_password:""
}
}
},
components: {
XInput,
Group,
XButton,
XHeader,
Toast
},
mounted : function() {

},
methods :{
routerback(){
this.$router.goBack();
},
login(){
var self=this;
/* 用户名格式检验 */
if(this.form.user_phone==''){
this.confrim="请输入正确的用户名";
this.toast=true;
return false;
}
/* 用户密码格式校验 */
if(this.form.user_password==''){
this.confrim="请输入正确的用户密码";
this.toast=true;
return false;
}
self.form.user_password=self.md5(self.md5(self.md5(self.form.user_password))+"boxiang");
/* 进行接口登录 */
API.login.pwdLogin(this.form).then((Response)=>{
if(Response.body.code==200){
Response=Response.body.data;
/* 触发vuex登录状态更改操作 */
let token=this.md5(Response.user_id)+Response.user_id;
let userInfo={
loginname : Response.nickname,
avatar : Response.face,
id : Response.user_id,
token : token,
};
localStorage.setItem("userInfo",JSON.stringify(userInfo));
/* 设置当前认证状态 */
localStorage.setItem("isCertify",Response.isCertify);
this.$store.dispatch('IsCertify');
this.$store.dispatch('SetUserInfo',userInfo);
if(!this.type){
this.$router.push({
path:'/index/main',
})
}else{
localStorage.setItem("goodsInfo","login");
this.routerback();
}
}else{
self.confrim=Response.body.msg;
this.toast=true;
}
})

},
regist(){
this.$router.push({
path:'/regist',
})
}
},
activated(){
this.type=this.$route.query.type;
if(this.type){
this.logincode="/logincode?type="+this.type;
}
}
}



