browserPay(key) {
if (key == 'menu1') {
/* 微信支付 */
this.payMethod = 5;
} else if (key == 'menu2') {
this.payMethod = 4;
} else {
return false;
}
API.order.orderShipPay({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderId: this.orderId,
}).then((res) => {
if (res.body.code == 200) {
let self = this;
let openId = localStorage.getItem("openId");
API.order.OrderWechat({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
orderSn: res.body.data.order_big_sn,
payMethod: this.payMethod,
openId: openId,
}).then((resopndy) => {
if (resopndy.body.code == 250) {
self.confrim = "支付完成";
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else if (resopndy.body.code == 300) {
self.confrim = resopndy.body.msg;
self.toast = true;
setTimeout(() => {
self.orderList[self.currentType] = [];
self.currentPage = 1;
self.haveData = true;
self.getTypeData(self.currentType);
}, 1500);
} else {
self.payTypeData = resopndy.body;
if (key == 'menu1') {
/* 微信支付 */
window.location.href = this.payTypeData;
} else if (key == 'menu2') {
const div = document.createElement('div');
div.innerHTML = this.payTypeData;
document.body.appendChild(div);
document.forms.alipaysubmit.submit();
}
}
}, (err) => {
self.confrim = "支付异常";
self.toast = true;
self.$router.push({
path: '/index/main/order'
})
});
}
});

