
import HeaderCop from './common/header.vue';
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'

import { mapState, mapActions } from 'vuex';
export default {
data() {
return {
transitionName: 'slide-right', // 默认动态路由变化为slide-right
currentindex: 0,/* 当前路由index值 */
title: "绘本",
}
},
directives: {
TransferDom
},
components: {
XHeader,
Actionsheet,
GroupTitle,
Tabbar,
TabbarItem,
Loading,
HeaderCop
},
computed: {

},
watch: {

},
mounted: function() {
setInterval(()=>{
switch (this.$route.fullPath) {
case '/book/main':
this.title="绘本";
break;
case '/book/main/collect':
this.title="我的收藏";
break;
case '/book/main/car':
this.title="借书架";
break;
default:
this.title="我的";
break;
}
},100)
},
activated(){

},
methods: {
/* 路由切换函数 */
routerchange(index) {
switch (index) {
case 0:
this.title="绘本";
break;
case 1:
this.title="我的收藏";
break;
case 2:
this.title="借书架";
break;
default:
this.title="我的";
break;
}
if (index < this.currentindex) {
this.transitionName = 'slide-right'
} else {
this.transitionName = 'slide-left'
}
this.currentindex = index;
}
}
}




