
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Toast from 'vux/src/components/toast/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'

import { mapGetters } from 'vuex'
import SinceCompent from '../order/sinceCompent.vue';
import { API, getQuery } from '../../services';

export default {
components: {
XHeader,
Group,
Checker,
CheckerItem,
SinceCompent,
Toast,
LoadMore
},
data() {
return {
canBottom:true,//是否可以到达最底部
loadshow:false,//显示更多
haveData:true,//是否有数据
page:1,//当前页码
confrim: "请选择物流方式",
toast: false,
val: "",
noTpl: false,
tplobj: {},
sinceList: [],//获取自提点列表
}
},
computed: {
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
])
},
methods: {
/* 路由回退 */
routerBack(){
if(this.val!=3){
this.$store.dispatch("CurrentTpl", this.val);
}
this.$router.goBack();
},
/* 数据初始化 */
dataForm() {
/* 获取物流方式 */
API.order.orderTpl({
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.$route.params.id
}).then((res) => {
if (res.body.code == 200) {
if (!res.body.data.tpl[0]) {
this.noTpl = true;
} else {
this.noTpl = false;
}
let tpl = {};
for (let item of res.body.data.tpl) {
tpl[item.goods_shipping_tpl_type] = 1;
}
this.tplobj = tpl;
if (tpl[3]) {
/* 获取自提点列表 */
API.order.goodsSince({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.$route.params.id,
page:this.page,
page_number:10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
this.sinceList = res.body.data.ztd.data;
if(res.body.data.ztd.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
}
});
}
}
});

},
/* 获取更多数据 */
getMoreData(){
if(this.val==3 && this.haveData){
this.loadshow=true;
API.order.goodsSince({
userId: this.getUserInfoUserId,
token: this.getUserInfoToken,
province: this.$store.state.addressData.province,
city: this.$store.state.addressData.city,
region: this.$store.state.addressData.district,
goodsId: this.$route.params.id,
page:this.page,
page_number:10,
}).then((res) => {
/*  */
if (res.body.code == 200) {
setTimeout(()=>{
this.sinceList = this.sinceList.concat(res.body.data.ztd.data);
this.canBottom=true;
this.loadshow=false;
},500);
if(res.body.data.ztd.data.length==10){
this.haveData=true;
this.page++;
}else{
this.haveData=false;
}
}
});

}else{
this.canBottom=true;
}
},
/* 触发状态管理 */
stateChange() {
if (this.val == "") {
this.toast = true;
return false;

} else if (this.val == "3") {
this.confrim = "请选择自提点";
this.toast = true;
return false;
}
else {
this.$store.dispatch("CurrentTpl", this.val);
this.$router.goBack();
}
},

},
mounted() {
overscroll(document.querySelector('.order_logmain'));

let self = this;
function getScrollTop() {
var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
if (document.body) {
bodyScrollTop = document.body.scrollTop;
}
if (document.documentElement) {
documentScrollTop = document.documentElement.scrollTop;
}
scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
return scrollTop;
}

function getScrollHeight() {
var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
if (document.body) {
bodyScrollHeight = document.body.scrollHeight;
}
if (document.documentElement) {
documentScrollHeight = document.documentElement.scrollHeight;
}
scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
return scrollHeight;
}

function getWindowHeight() {
var windowHeight = 0;
if (document.compatMode == "CSS1Compat") {
windowHeight = document.documentElement.clientHeight;
} else {
windowHeight = document.body.clientHeight;
}
return windowHeight;
}
window.onscroll = function() {
if (getScrollTop() + getWindowHeight() <= (getScrollHeight()-10)) {
if(self.canBottom==true){
self.canBottom=false;
self.getMoreData();
}
}
};
},
activated() {
overscroll(document.querySelector('.order_logmain'));
this.val=0,
this.page=1;
this.canBottom=true;
this.haveData=true;
this.sinceList=[];
this.dataForm();
}
}



