
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex';
import {API,getQuery} from '../../services';


export default {
components: {
XHeader,
Group,
Cell,
},
data () {
return {

typeList:[
{
name:"未使用",
click:true,
id:0,
},
{
name:"已使用",
click:false,
id:1,
},
{
name:"已过期",
click:false,
id:2,
}
],
cardList:[{
amount:"40.00",
coupon_no:"201709182315482kd91ybk",
rule_intro_url:"",
store_id:75,
store_name:"万享旅行租赁",
type:1,
use_end_time:1508339747,
use_minimal_amount:"0.00",
use_range:2,
use_start_time:1505747748,}
]
}
},
mounted(){
this.getData(0);
},
computed:{
...mapGetters([
'getUserInfoUserId',
'getUserInfoToken',
'getAddress',
'getNamePhone'
]),
},
methods:{
routerBack(){
this.$router.goBack();
},
typeClick(item){
if(item.click){
return false;
}
for(let item of this.typeList) {
item.click=false;
}
item.click=true;
this.getData(item.id);
},
goShop(item){
if(this.typeList[0].click){
if(item.store_id){
this.$router.push({
path:"/shop/"+item.store_id,
});
}else{
this.$router.push({
path:"/"
});
}
}
},
getData(id){
API.card.myCardList({
userId:this.getUserInfoUserId,
token:this.getUserInfoToken,
useType:id,
page_number:10,
page:1,
}).then((res)=>{
if(res.body.code==200){
this.cardList=res.body.data.data;
}
})
}
}
}



