import { API, getQuery } from '../../../services';
import { mapGetters } from 'vuex';
export default {
    data() {
        return {
          
        }
    },
    props: {
        commonBookList: Array,
        type: String,
        refresh:Function
    },
     computed: {
        ...mapGetters([
            'getUserInfoUserId',
            'getUserInfoToken',
        ]),
    },
    methods: {
        /**@argument
         * 加入收藏
         */
        addColl(item,index){
              if(!this.getUserInfoUserId){
                this.$router.push({
                    path:"/login"
                })
                return false;
            }
            if(item.is_collected==0){
                API.book.addColl({
                user_id: this.getUserInfoUserId,
                token: this.getUserInfoToken,
                book_ids:[item.book_id]
            }).then((res) => {
                if (res.body.code == 200) {
                   item.is_collected=1;
                    this.$vux.toast.show({
                    text: '收藏成功'
                });
                }
            })
            }else{
                API.book.cancelColl({
                user_id: this.getUserInfoUserId,
                token: this.getUserInfoToken,
                book_ids:[item.book_id]
            }).then((res) => {
                if (res.body.code == 200) {
                   item.is_collected=0;
                   
                    this.$vux.toast.show({
                    text: '取消收藏'
                });
                if(this.$route.fullPath=="/book/main/collect"){
                    this.commonBookList.splice(index,1);
                }
                }
            })
            }
        },
        /**@argument
         * 加入购物车
         */
        addCar(item){
           if(!this.getUserInfoUserId){
                this.$router.push({
                    path:"/login"
                });
                return false;
            }
            if(item.is_shelf==0){
                API.book.addCar({
                user_id: this.getUserInfoUserId,
                token: this.getUserInfoToken,
                book_ids:[item.book_id]
            }).then((res) => {
                if (res.body.code == 200) {
                   item.is_shelf=1;
                    this.$vux.toast.show({
                    text: '成功加入借书架'
                });
                }else if(res.body.code == 250){
                    this.$router.push({
                        path:"/book_bookCard"
                    })
                }
            })
            }else{
                this.$vux.toast.show({
                    text: '已加入借书架'
                });
            }
             
        },
        /**
         * 返回当前设备类型，1：安卓 2：ios 3：H5
         * @returns 
         */
        typeCheck(){
            var u = navigator.userAgent;
            var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1; //android终端
            var isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); //ios终端
            if(isAndroid){
                return 1;
            }else if(isiOS){
                return 2;
            }else{
                return 3;
            }
        },
        /* 进入商品详情 */
        enterInfo(id,series="",name="") {
            /* 原生事件互动 */
            if(localStorage.getItem('isAPP')==1){
                Android.goodClick(id);  
            }
            else if(localStorage.getItem('isAPP')==2){
                window.open("goodClick?goods_id="+id);
            }else{
                /* 列表位置回滚 */
                let scroll=document.querySelector(".wapper_content").style.transform.split(" ")[1].split("p")[0];
                window.stopReload=true;
                localStorage.setItem("bookScroll",scroll);
                if(this.type!=2){
                    this.$router.push({
                        name:"B_bookInfo",
                        params:{
                          id
                        }
                    })
                   /*  window.location.href="/#/book_bookInfo/"+id; */
                }else{
                    this.$router.push({
                        path:"/book_bookSeriesList",
                        query:{
                            series:series,
                            name:name
                        }
                    })
                    
                }
                
            }
        },
    },
    
    activated () {
        /*setTimeout(()=>{
            if(this.$route.fullPath.split("/"[1])!="book_bookInfo"){
                document.querySelector(".wapper_content").style.transform=localStorage.getItem("bookScroll");    
                localStorage.setItem("bookScroll","");
            }
        },100); */
    }
}



