
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapGetters } from 'vuex';
import { API, getQuery } from '../../services';

export default {
data() {
return {
user:{
name:"网二",
img:"../../assets//img//index//avatar.png"
},
zmed:false,
relief_limit:''
}
},
components: {
XHeader,
Tabbar,
TabbarItem,
Cell,
Group
},
computed:{
...mapGetters([
'getIsCertify',
'getUserInfoUserId',
])
},
activated () {
this.userZMReliefInfo();
},
mounted(){
/* 获取当前的认证状态 */
this.$store.dispatch('IsCertify');

},
methods :{
/*获取芝麻信用减免额度*/

userZMReliefInfo(){
API.person.getUserZMReliefInfo({
user_id: this.getUserInfoUserId
}).then((res) => {
if (res.body.code == 200) {
this.zmed = true;
this.relief_limit = '剩余免押金额度' + res.body.data.tmp_relief_limit + '元';
}else{
this.relief_limit = '去授权';
}
})
},
authorization(){



this.$router.push({
path:'/authInfo'
})
}
}
}



